<template>
  <div class="columns">
    <div class="column is-2">
      <aside class="menu">
        <ul class="menu-list" v-for="user in users">
          <div class="container" style="padding: 5px">
            <li>
              <a>
                {{ user.username }}
                <span v-if="user.self">(You)</span>
              </a>
            </li>
          </div>
        </ul>
      </aside>
    </div>
    <div class="column" style="margin-top:5%;">
      <div class="container" id="chatbox" style="margin-right: 30%">
        <div  v-for="(message, index) in messages"
          :key="index">
        <h2 v-if="users[0].username == message.user"
          class="subtitle has-text-right"
        >{{ message.message }}</h2>
        <h2 v-else
          class="subtitle"
        ><b>{{ message.user }}</b>: {{ message.message }}</h2>
        </div><br/>
        <textarea class="textarea" v-model="input" placeholder="e.g. Hello world"></textarea>
        <div class="button is-success" @click="sendMessage($event, this.input)">Send</div>
      </div>
    </div>
  </div>
</template>
<script>
import socket from '../../socket'

export default {
  name: "DoctorChat",
  data() {
    return {
      users: [],
      selectedUser: [],
      input: null,
      messages: [],
      currentUser: null
    }
  },
  mounted() {
    socket.on("user connected", (user) => {
      this.users.push(user);
    });
    socket.on("users", (users) => {
      users.forEach((user) => {
        user.self = user.userID === socket.id;
      });
      // put the current user first, and then sort by username
      this.users = users.sort((a, b) => {
        if (a.self) return -1;
        if (b.self) return 1;
        if (a.username < b.username) return -1;
        return a.username > b.username ? 1 : 0;
      });
    });
    socket.on('chat message', (msg, user) => {
      this.messages.push({
        user: user,
        message: msg
      })
    });
  },
  methods: {
    sendMessage(e, msg) {
      if (this.input === null) {
        e.preventDefault();
      } else {
        console.log(this.users.find(x => x.self === true))
        this.currentUser = this.users.find(x => x.self === true)
        socket.emit('message', msg, this.currentUser.username)
        this.input = null
      }
    }
  }
}
</script>
<style scoped>
@media (max-width: 991.98px){
  #chatbox {
    margin-right: 0 !important;
  }
}
</style>