<template>
  <section class="hero is-fullheight">
    <div class="hero-body">
      <div class="container">
        <h1 class="title">Example only: input fullusername/username</h1>
        <div class="field">
          <label class="label">username</label>
          <div class="control">
            <input class="input" v-model="this.username" type="text" placeholder="e.g Alex Smith" />
          </div>
          <div class="control">
            <button class="button is-primary" @click="signUpSuccess(this.username)">Confirm</button>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import axios from 'axios'
import socket from '../../socket'

export default {
  username: "UserSignUp",
  data() {
    return {
      username: null,
      usernameAlreadySelected: false
    }
  },
  methods: {
    async signUpSuccess(username) {
      this.usernameAlreadySelected = true;
      socket.auth = { username };
      socket.connect();

      await this.$router.push(`/${this.username}/chatnow`)
    }
  }
}
</script>